import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ExchangePointsComponent } from './exchange-points.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { IonicModule } from '@ionic/angular';
import { TranslateModule } from '@ngx-translate/core';
import { ToCouponsComponent } from './to-coupons/to-coupons.component';
import { FormsModule } from '@angular/forms';

const routes: Routes = [
  {
    path: '',
    component: ExchangePointsComponent
  }, {
    path: 'to-coupons',
    component: ToCouponsComponent
  }, 
]

@NgModule({
  declarations: [
    ExchangePointsComponent,
    ToCouponsComponent,
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    IonicModule,
    TranslateModule,
    FormsModule
  ]
})
export class ExchangePointsModule { }
